import React from "react";
import {connect} from "react-redux";
import {getSelectedOfferType} from "eshop/modules/configurator/selectors/filters"
 class ProductCartRegulationAndPriceListComponent extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this._loadModules();
    }

    toHTML(content) {
        return {__html: content};
    }

    render() {

        if(this.props.selectedOfferType!=="SIMFREE"){
                return null;
            }
        return (
            <div className="regulation-price-list">
                <div dangerouslySetInnerHTML={this.toHTML(this.props.content)}/>
            </div>
        )
    }

     _loadModules() {
     if(document.getElementById("opl-expander-regulation-section"))
        OPL.UI.loadModules(document.getElementById("opl-expander-regulation-section"), [{
            path: 'core/modules/expander',
            options: {
                triggerSelector: '.opl-section__expander-trigger',
                contentSelector: '.opl-section__content',
                parentClass: 'opl-section--togglable'
            }
        }]);
    }
}

const mapStateToProps = state => ({
    selectedOfferType: getSelectedOfferType(state)
})

const mapDispatchToProps = dispatch => ({
})

export default connect(mapStateToProps, mapDispatchToProps)(ProductCartRegulationAndPriceListComponent)

